import React from "react"
import { Link } from "gatsby"

import {  DialogOverlay, DialogContent } from "@reach/dialog";
// import modals from "../css/modals.module.css"
// import Search from "./search"
import {Helmet} from "react-helmet"
import {useTransition, animated} from 'react-spring'
import Search from "../components/search"
// import Img from "gatsby-image"
// import imgStyle from "../css/img.module.css"
// import home from "../css/home.module.css"
import close1 from "../images/closeblack.png"
// import Search from "../components/search"
import burger from "../images/burger.png"
import linkStyle from"../css/link.module.css"
import icon from"../css/icon.module.css"
import menu from "../css/menu.module.css"
import "@reach/dialog/styles.css";
import dialog from "../css/dialog.module.css"




function Modals(props) { 
  
  const open = () => setShowDialog(true);
  const close = () => setShowDialog(false);
  const AnimatedDialogOverlay = animated(DialogOverlay);
  const AnimatedDialogContent = animated(DialogContent);
  const [showDialog, setShowDialog] = React.useState(false);
  const transitions = useTransition(showDialog, null, {
    from: { opacity: 0, y: -10 },
    enter: { opacity: 1, y: 0 },
    leave: { opacity: 0, y: 10 }
  });
    return (
      <div>
        <Helmet >
     
      <style type="text/css">{`
        body {
            background-color: var(--cream);
            color: var(--blue);
        }
        
    `}</style>
   
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"/>
    
      </Helmet>
      <button onClick={open}className={menu.button}><img src={burger} alt="burger" className={linkStyle.burger}/></button>
      {transitions.map(
        ({ item, key, props: styles }) =>
          item && (
            <AnimatedDialogOverlay style={{ opacity: styles.opacity }}
            isOpen={showDialog}
        onDismiss={close}
        className={dialog.a1}>
              <AnimatedDialogContent
                style={{
                  transform: styles.y.interpolate(
                    value => `translate3d(0px, ${value}px, 0px)`
                  ),
                  // border: "4px solid hsla(0, 0%, 0%, 0.5)",
                  // borderRadius: 10,
                  boxShadow: "0px 10px 50px hsla(0, 0%, 0%, 0.33)"
                }
                 }
                
          className={dialog.a2}
              >
                 <div className={icon.home}>
            
            <a href="https://www.facebook.com/muhzulzidan"> <i class="fa fa-facebook-square fa-1x"></i></a>
            <a href="https://www.instagram.com/muhzulzidan/"> <i class="fa fa-instagram fa-1x"></i></a>
            <a href="https://www.linkedin.com/in/muh-zulzidan-5189a6192/"><i class="fa fa-linkedin"></i></a>
            <a href="https://twitter.com/mzulzidan1"><i class="fa fa-twitter"></i></a>
            <div className={dialog.a4}>
                  <button onClick={() => setShowDialog(false)}
                  className={icon.close1}>
                    <img src={close1} alt=""className={icon.close1img}/>
                  </button>
                  </div>
         </div>
                
                
                <br/>
                <Link
              className={linkStyle.homenav}
              to={`/teknologi`}
              
              activeClassName={linkStyle.activehomenav}>
              teknologi
            </Link>
            <Link
              className={linkStyle.homenav}
              to={`/statistik`}
              
              activeClassName={linkStyle.activehomenav}>
             statistik
            </Link>
            <Link
              className={linkStyle.homenav}
              to={`/kurikulum`}
              
                activeClassName={linkStyle.activehomenav}>
             kurikulum
            </Link>
            <Link
                className={linkStyle.homenav}
                to={`/ekonomi`}
                 
              >
               ekonomi
              </Link>
              <Link
                className={linkStyle.homenav}
                to={`/desain`}
                 
              >
                desain
              </Link>
              <Link
                className={linkStyle.homenav}
                to={`/corona`}
                 
              >
                corona
              </Link>
              <Link
                className={linkStyle.homenav}
                to={`/pengembangan-diri`}
                 
              >
                pengembangan diri
              </Link>
              
              <br></br>
              <Search></Search>
              </AnimatedDialogContent>
            </AnimatedDialogOverlay>
          )
      )}
    </div>
    )
}

export default Modals
