import React from 'react';
import { Link } from 'gatsby';
import { rhythm } from "../utils/typography"
import {Helmet} from "react-helmet"
import pager from "../css/pager.module.css"
const Pager = ({ pageContext }) => {
  console.log(pageContext);
  const { previousPagePath, nextPagePath } = pageContext;
  return (
    
    <nav style={{ display: 'flex', justifyContent: 'space-between', 
    marginTop:rhythm(1)}} >
      <Helmet>
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>

    </Helmet>
      <div>
        {previousPagePath && (
         <div>
            <Link to={previousPagePath} >
            <div className={pager.before}>halaman sebelumnya</div>
            <div className={pager.mobilebefore}><i className="fa fa-chevron-circle-left"></i></div>
            </Link>
            
         </div>
        )}
       
        
      </div>

      <div style={{ justifySelf: 'flex-end' }} >
        {nextPagePath && (
          <div>
            <Link to={nextPagePath}>
              <div className={pager.after}>halaman berikutnya</div>
              <div className={pager.mobileafter}><i className="fa fa-chevron-circle-right"></i></div>
            </Link>
            
          </div>
        )}
        
      </div>
      
    </nav>
  );
};

export default Pager;