import React from "react"
import { Link, graphql } from "gatsby"
import HomeLayout from "../components/homelayout"
import Img from "gatsby-image"
import SEO from "../components/seo"
import Pager from "../components/pager"
import lindex from "../css/lindex.module.css"


export const desainQuery = graphql`
query DesainQuery ($skip: Int!, $limit: Int!) {
allCockpitPost(filter: {lang: {eq: "id"}, published: {value: {eq: true}}, tags: {value: {eq: "desain"}}}, sort: {order: DESC, fields: date___value} limit: $limit, skip: $skip) {
  edges {
    node {
      title { 
        value
        slug
      }
      image { 
        value {
          childImageSharp {
            fluid(maxWidth: 600) {
                   ...GatsbyImageSharpFluid
             }
          }
        }
      }
      description { 
        value {
          childMarkdownRemark {
            excerpt(pruneLength: 160)
          }
        }
      }
      content { 
          value {
            childMarkdownRemark {
              frontmatter {
                author
              }
            }
          }
        }
        date {
          value(formatString: "MMMM DD, YYYY")
        }
    }
  }
}
}
`

class Desain extends React.Component {
    render() {

  const { data } = this.props
  const posts = data.allCockpitPost.edges
  const { pageContext } = this.props
  return (
    <HomeLayout location={this.props.location} >
    <SEO title=" muhzulzidan" />
    <ul style={{ listStyle: "none" }}
    className={lindex.ul}>
      {posts.map(({ node,  }) => {
        return (
        <li 
        className={lindex.li}>
          <Link to={`${node.title.slug}`} 
          className={lindex.link}>
           <div className={lindex.div1}>
              <Img fluid={node.image.value.childImageSharp.fluid} alt={node.title.value}  
              className={lindex.img}/>
           </div>
            <div className={lindex.div2}>
              <h3 dangerouslySetInnerHTML={{ __html: node.title.value }} className={lindex.h3} />
              <p style={{ margin: 0, color: "grey" }} className={lindex.written}>
                Written by {node.content.value.childMarkdownRemark.frontmatter.author} on {node.date.value}
              </p>
              <div dangerouslySetInnerHTML={{ __html: node.description.value.childMarkdownRemark.excerpt }} className={lindex.div3}>
          </div>
            </div>
          </Link>
          
        </li>)
     })}
    </ul>
    <Pager pageContext={pageContext} />
  </HomeLayout>

  )}}
export default Desain

